<template>
<div>
    <div class="Login" v-if="isLog">
        <h3>登录</h3><br>
        <el-input placeholder="请输入用户名" v-model="user">
        <template slot="prepend">用户:</template>
    </el-input><br>
    <el-input placeholder="请输入密码" v-model="pass">
        <template slot="prepend">密码:</template>
    </el-input><br>
        
    <div>
        <el-button type="primary" style="margin:0px 10px;width:100%" @click="onSubmit">登录</el-button>
        <el-button style="border:0px solid black" @click="goRegi"> 还没账号，点击注册</el-button>
    </div>
    
    </div>
    <div class="Login" v-if="isRegi">
        <h3>注册</h3><br>
        <el-input placeholder="请输入用户名" v-model="regiuser">
        <template slot="prepend">用户:</template>
    </el-input><br>
    <el-input placeholder="请输入密码" v-model="regipass">
        <template slot="prepend">密码:</template>
    </el-input><br>
    <div>
        <el-button type="primary" style="margin:0px 10px;width:100%" @click="onRegister">注册</el-button>
        
    </div>
    
    </div>
</div>


</template>
<script>
export default {
    data(){
        return{
        user:null,
        pass:null,
        isRegi:false,
        isLog:true,
        regipass:null,
        regiuser:null
        }
       
    },
    mounted(){
        console.log(this.$route.query.redirect)
    },
    methods:{
        goRegi(){
            this.isRegi=true;
            this.isLog=false;
        },
        onRegister(){
        this.$axios.post("/users",{
          params:{user:this.regiuser,pass:this.regipass,type:"register"}
        }).then((response)=>{
          let res=response.data;
          if(res.status=="0"){
              if(res.result==false){
                  alert("用户已存在")
              }else{
            this.$store.dispatch('UserLogin', true);
            
            this.$store.dispatch('UserName',this.regiuser)
            this.$router.push('../tabs')
          }
          }
        })
        },
        onSubmit(){
        this.$axios.post("/users",{
          params:{user:this.user,pass:this.pass,type:"login"}
        }).then((response)=>{
          let res=response.data;
          if(res.status=="0"){
              if(res.result==false){
                  alert("用户名或密码错误")
              }else{
            this.$store.dispatch('UserLogin', true);
            this.$store.dispatch('UserName',this.user)
            // store.commit('UserLogin',true)
            this.$router.push(this.$route.query.redirect)
          }
          }
        })
        }
    }
}
</script>

<style scoped>
    h3{
        margin: 0px;
        padding: 0px;
    }
    .el-input{
        margin: 10px 10px;
       
    }
    .beforeRegi{
        text-align: left;
    }
    .Login{
         width: 350px;
        padding: 20px;
        border: 1px solid #ddd;
        margin: 20px auto;
    }
</style>




