<template>
<div id="#sail">
    <div style="height:20px;"></div>
<el-form ref="form" :model="form" label-width="150px" label-position="right" size="mini" :rules="rules">
  <el-form-item label="物品名称" prop="name" >
    <el-input v-model="form.name" placeholder="请输入物品名称"></el-input>
  </el-form-item>
  <el-form-item label="物品类别"  prop="type">
    <el-select v-model="form.type" placeholder="请选择物品类别" style="float:left">
      <el-option label="书籍教材" value="book"></el-option>
      <el-option label="电脑手机" value="computer"></el-option>
      <el-option label="数码产品" value="digital"></el-option>
      <el-option label="体育器材" value="gym"></el-option>
      <el-option label="生活用品" value="lifeuses"></el-option>
      <el-option label="其他物品" value="others"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="交易地点" prop="place">
    <el-input v-model="form.place" placeholder="请输入交易地点"></el-input>
  </el-form-item>
   <el-form-item label="联系方式" prop="connact">
    <el-input v-model="form.connact" placeholder="请输入联系方式"></el-input>
  </el-form-item>
    <el-form-item label="物品价格" prop="price">
       <el-input placeholder="请输入价格" v-model="form.price">
        <template slot="prepend">￥</template>
        </el-input>
  </el-form-item>
    <el-form-item label="物品详情" prop="discript">
    <el-input type="textarea" v-model="form.discript" placeholder="请输入对物品的描述"></el-input>
  </el-form-item>
 <el-form-item label="图片上传">
 <el-upload
 style="float:left"
  class="upload-demo"
  action="/files"
  :on-preview="handlePreview"
  :on-remove="handleRemove"
  :on-success="UploadSuccess"
  :file-list="fileList"
  list-type="picture">
  <el-button size="small" type="primary" >点击上传</el-button>
  <span slot="tip" class="el-upload__tip">（只能上传jpg/png文件，且不超过500kb）</span>
</el-upload>

  </el-form-item>
 
  <el-form-item>
    <el-button type="primary" @click="onSubmit('form')">提交</el-button>
    <el-button @click="resetForm('form')">重置</el-button>
  </el-form-item>
</el-form>
</div>
</template>
<script>
export default {
  
    data(){  
      // var checkName = (rule,value,callback) => {
      //   console.log(value)
      //   if (!value) {
      //     return callback(new Error('物品名称不能为空'));
      //   }
      //   // setTimeout(() => {
      //   //   if (!parseInt(value)) {
      //   //     callback(new Error('请输入数字值'));
      //   //   } else {
      //   //     if (value < 18) {
      //   //       callback(new Error('必须年满18岁'));
      //   //     } else {
      //   //       callback();
      //   //     }
      //   //   }
      //   // }, 1000);
      // }; 
      var checkPrice = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('价格不能为空'));
        }
        setTimeout(() => {
          if (!parseInt(value)) {
            callback(new Error('请输入数字值'));
          } else {
            if (value < 18) {
              callback(new Error('必须超过0元'));
            } else {
              callback();
            }
          }
        }, 1000);
      };
        return{
       
        form: {
          name: '',
          type: '',
          place: '',
          price: '',
          discript: '',
          connact:'',
          src:'',
          age:''
        },
        fileList:[],
        rules:{
           name: [
            { required: true, message: '请输入物品名称', trigger: 'blur' },
            { min: 2, max: 8, message: '长度在 2到 8个字符', trigger: 'blur' }
          ],
          type: [
            { required: true, message: '请选择活动区域', trigger: 'change' }
          ],
           place: [
            { required: true, message: '请输入交易地点', trigger: 'blur' },
            { min: 2, max: 8, message: '长度在 2到 8个字符', trigger: 'blur' }
          ],
          connact:[
            { required: true, message: '请输入联系方式', trigger: 'blur' }
          ],
          discript:[
             { required: true, message: '请输入物品描述', trigger: 'blur' },
            { min: 10, max: 100, message: '长度在 10到 100个字符', trigger: 'blur' }
          ],
          price:[
            { validator: checkPrice, trigger: 'blur'}
          ],
          upload:[
            { required: true, message: '请输入物品描述', trigger: 'blur' }
          ]

          }        
    }
    },
    methods:{
      imageUpload(){
      },
      UploadSuccess(res){
        console.log(res.result);
        this.form. src='../../../../'+res.result
      },
      onSubmit(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log(formName)
            if(this.form.src==''){
              alert('请上传图片')
            }else{
               this.$axios.post("/goods",{
          params:{
            name:this.form.name,
            type:this.form.type,
            place:this.form.place,
            price:this.form.price,
            discript:this.form.discript,
            connact:this.form.connact,
            src:this.form.src
          }
        }).then((response)=>{
          let res=response.data;
          if(res.status=="0"){
              if(res.result==false){
                  alert("数据提交失败")
              }else{
            this.$refs['form'].resetFields();
            this.fileList=[];
            alert('数据提交成功!');
          }
          }
        })
            }
         
          
          } else {
            alert('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
    handleRemove(file, fileList) {
        console.log(file, fileList);
    },
    handlePreview(file) {
        console.log(file);
    },
    handleSuccess(file){
        console.log(file)
    },
    handleExceed(){

    },
    uploadError(){

    },
    uploadSuccess(){

    },
    onRemoveTxt(){

    },
    onBeforeUpload(){

    }

    }
}
</script>

<style scoped>
  .el-row {
    margin-top: 20px;
  }
  .el-col {
    border-radius: 4px;
    text-align: right;
    line-height: 36px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
    line-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>





