<template>
    <div class="help">
<el-steps :active="active" align-center>
  <el-step title="步骤1"  description="在校园二手网站进行注册，成为二手市场的医院" @click="change"></el-step>
  <el-step title="步骤2" description="在二手选购选择你喜欢的商品，点击物品得到联系方式，交易地点等信息，
    与买主联系并且在线下完成交易"></el-step>
  <el-step title="步骤3" description="在二手出售选择你要出售的二手物品，完善物品的信息，并且上传一张图片，商品
  便可在网站展示，等待别人选购"></el-step>
  <el-step title="步骤4" description="通过二手交易，可以使我们的闲置物品有了新的价值，也帮助了同校的师弟师妹们，
  希望同学们踊跃地加入到这个群体中来"></el-step>
</el-steps>
<div>
    <el-button style="margin-top: 50px;" @click="change">下一步</el-button>
</div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            active:1
        }
    },
    methods:{
        change(){
            if (this.active++ > 3) this.active = 0;
        },
    }
}
</script>
<style>
    .help{
        margin-top: 100px;
    }
</style>





