<template>
    <div>
        <el-row class="row" >
            商品详情页
        </el-row>

<el-row >
    <el-col :span="8">
      <div class="grid-content bg-purple">
          <img :src="goodItem.productSrc" alt="" width="300px" height="300px">
          <div class="goback">
              <router-link to="../tabs">
                <span>回到上一页</span>
              </router-link>
          </div>
      </div>
    </el-col>
  <el-col :span="16">
    <div class="grid-content bg-purple-light">
        <el-row class="row">
            <el-col :span="8" class="itemcol1">商品名称：</el-col>
            <el-col :span="16" class="itemcol2"> {{goodItem.productName}}</el-col>   
        </el-row>
        <el-row class="row">
            <el-col :span="8" class="itemcol1">商品价格：</el-col>
            <el-col :span="16" class="itemcol2"> {{goodItem.productPrice}}</el-col>   
        </el-row>
        <el-row class="row">
            <el-col :span="8" class="itemcol1">交易地点：</el-col>
            <el-col :span="16" class="itemcol2"> {{goodItem.productPlace}}</el-col>   
        </el-row>
        <el-row class="row">
            <el-col :span="8" class="itemcol1">联系方式：</el-col>
            <el-col :span="16" class="itemcol2"> {{goodItem.productConnact}}</el-col>   
        </el-row>
        <el-row class="row">
            <el-col :span="8" class="itemcol1" style="height:100px;line-height:100px">商品描述：</el-col>
            <el-col :span="16" class="itemcol2" style="height:100px">{{goodItem.productDescript}}</el-col>   
        </el-row>
        
    </div>
</el-col>
</el-row>
    </div>
</template>
<script>
export default {
     mounted:function(){
         console.log(this.$route.params.id)
         this.$axios.get('/goods',
         {params:{goodId:this.$route.params.id}
         }).then((Response)=>{
             let res=Response.data;
             console.log(res.result)
             this.goodItem=res.result;
         })
     },
     data(){
         return{
             goodItem:{}
         }
     }

}
</script>
<style>  
.itemcol1{
    background:  #e5e9f2;
    height: 50px;
    line-height: 50px;
}
.itemcol2{
    background: #d3dce6;
    height: 50px;
    line-height: 50px;
    overflow-x: hidden;
}
.row{
    margin-bottom: 20px;
    height: 50px;
}
.goback{
    margin-top: 50px;
    font-size: 14px;
}
</style>





