<template>
<div class="tabs">
  <el-menu
  :default-active="activename"
  class="el-menu-demo"
  mode="horizontal"
  @select="handleSelect"
  router>
  <el-menu-item index="1"  disabled style="opacity:1 !important;padding:0px  100px 0px 20px !important;">
      <img src="../../assets/logo2.png" alt="logo" width="200px" height="60px">
  </el-menu-item>
  <el-menu-item index="../tabs">
    二手选购
  </el-menu-item>
  <el-menu-item index="../sail"> 
    二手出售
  </el-menu-item>
    <el-menu-item index="../help"> 
    帮助中心
  </el-menu-item> 
  <div class="loginout">
     <span  v-if="this.$store.state.token"> 
   欢迎你，{{$store.state.username}}
   </span>
  <router-link to="../login" v-if="!this.$store.state.token"> 
    登录/注册
  </router-link>   
  <router-link to="../login" v-if="this.$store.state.token"
  @click.native="logOut"> 
    注销
  </router-link>
  </div>


</el-menu>
<div style="height:500px;width:100%"><router-view class="tabs_view"></router-view></div>
</div>
</template>
<script>
  export default {
    data() {
      return {
        activename:'../tabs'
      };
    },
    mounted:function(){
      
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      logOut(){
        alert("click")
        console.log(this.$store.state.token)
        this.$store.dispatch('UserLogout')
      }
    }
  }
</script>
<style >
.tabs{
 position: relative;
}
.loginout{
  position: absolute;
  top: 25%;
  right: 10%;
  border: 0px solid black;
 outline: none;
}
a{
  text-decoration:none;
}
a:link,a:visited{
 text-decoration:none;
 
   /*超链接无下划线*/
}
.tabs_view{
  width: 1230px;
  margin: auto;
}
.el-menu-item{
  font-size: 16px ;
  font-weight: 20;
}
</style>



