<template>
<div>
  <ul>
    <li v-for="(item,itemIndex) in getdata" :key="item.index"
  @click="handleClick(item._id)"
    v-bind:class="{hasMargin:(itemIndex<6)}" >  
        <div class="img_container" slot="reference">
          <img :src="item.productSrc" :alt="item.productSrc" width="150px" height="150px">
        </div>
      <div >
        <div>{{item.name}}</div>
        <div class="msgpane">
          <div class="price">￥{{item.productPrice}}</div> 
          <div class="place"><i class="el-icon-location"></i>{{item.productPlace}}</div>
        </div>
        
      </div>
    </li>
  </ul>
</div>

</template>
<script>
export default {
    props:["getdata"],
    data(){
      return{
        active:true,

      } 
      
    },
    computed:{
      activeItem:function(){
        if(item.index>6){
          return false;
        }
        else{
          return true;
        }
      }
    },
    methods:{
      handleClick(index){
        console.log(index)
        this.$router.push(`../detail/${index}`)
      }
    }
       
    }
</script>
<style scoped>
ul li{
  text-decoration: none;
  float:left;
  list-style: none;
  margin: 0px 20px 0px 0px ;
}
ul li img:hover{
  border: 1px solid firebrick;
}
.msgpane{
  position: relative;
}
.price{
  float: left;
  color: firebrick;
}
.place{
  float: right;
  color: lightblue;
}
.img_container{
  padding: 2px;
  background-color: #F2F6FC;
  width: 150px;
  height: 150px;
}
.hasMargin{
    margin:  0px 20px 50px 0px;
}
.noMargin{
    margin:  0px 20px 0px 0px;
}
</style>





